import { Routes, Route, useLocation } from 'react-router-dom';
import { useEffect } from 'react';
import Projects from './pages/Projects';
import About from './pages/About';
import NotFound from './pages/NotFound';

// Import individual project pages
import Leo from './pages/projects/Leo';
import ModifiM1 from './pages/projects/ModifiM1';
import SmartCanister from './pages/projects/SmartCanister';
import RomanoCoffeeGrinder from './pages/projects/RomanoCoffeeGrinder';

function App() {
  const location = useLocation();

  useEffect(() => {
    // Set the background color based on the current route
    switch (location.pathname) {
      case '/':
        document.body.style.backgroundColor = '#F9F4F0';
        break;
      case '/about':
        document.body.style.backgroundColor = '#F9F4F0';
        break;
      case '/leo':
        document.body.style.backgroundColor = '#F9F4F0';
        break;
      case '/modifi-m1':
        document.body.style.backgroundColor = '#D3D4DC';
        break;
      case '/smart-canister':
        document.body.style.backgroundColor = '#D2D8DC';
        break;
      case '/romano-coffee-grinder':
        document.body.style.backgroundColor = '#EAEBEE';
        break;
      default:
        document.body.style.backgroundColor = '#FFFFFF';
        break;
    }
  }, [location]);

  return (
    <Routes>
      {/* Main Pages */}
      <Route path="/" element={<Projects />} />
      <Route path="/about" element={<About />} />

      {/* Individual Project Pages */}
      <Route path="/leo" element={<Leo />} />
      <Route path="/modifi-m1" element={<ModifiM1 />} />
      <Route path="/smart-canister" element={<SmartCanister />} />
      <Route path="/romano-coffee-grinder" element={<RomanoCoffeeGrinder />} />

      {/* Catch-All */}
      <Route path="*" element={<NotFound />} />
    </Routes>
  );
}

export default App;