import React, { useState } from 'react';
import { AiOutlineClose, AiOutlineMenu } from 'react-icons/ai';
import { Link } from 'react-router-dom';

const Navbar = () => {
  const [nav, setNav] = useState(false);

  const handleNav = () => {
    setNav(!nav);
  };

  const closeNav = () => {
    setNav(false); // Closes the menu when a link is clicked
  };

  return (
    <div>
    <div className="fixed top-0 right-0 bg-white/10 backdrop-blur-md h-[70px] text-[#201F1F]/80 z-20 flex justify-end items-center px-5 m-4 rounded-full shadow-sm">
      {/* Desktop Navigation */}
      <ul className="hidden md:flex tracking-wider">
        <Link to="/" onClick={closeNav}>
          <li className="p-5 font-bold hover:text-[#888888] hover:scale-105 transition duration-200">
            PROJECTS
          </li>
        </Link>
        <Link to="/about" onClick={closeNav}>
          <li className="p-5 font-bold hover:text-[#888888] hover:scale-105 transition duration-200">
            ABOUT
          </li>
        </Link>
      </ul>

      {/* Mobile Menu Button */}
      <div
        onClick={handleNav}
        className="block md:hidden transition duration-300 cursor-pointer"
      >
        {nav ? <AiOutlineClose size={24} /> : <AiOutlineMenu size={24} />}
      </div>

    
    </div>
      {/* Mobile Navigation */}
      <div
        className={`fixed top-0 left-0 h-full w-[60%] bg-white/20 backdrop-blur-md hadow-lg ring-1 ring-black/5 z-20 ease-in-out duration-500 ${
          nav ? 'translate-x-0' : '-translate-x-full'
        }`}
      >
        <ul className="p-8 text-xl text-left tracking-wide text-[#201F1F]/80">
          <Link to="/" onClick={closeNav}>
            <li className="p-5 font-bold hover:text-[#888888] hover:scale-105 transition duration-200">PROJECTS</li>
          </Link>
          <Link to="/about" onClick={closeNav}>
            <li className="p-5 font-bold hover:text-[#888888] hover:scale-105 transition duration-200]">ABOUT</li>
          </Link>
        </ul>
      </div>
    </div>
  );
};

export default Navbar;