import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import leoHero from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/LEOHero.png";
import modifiM1Hero from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/ModifiM1Hero.png";
import smartCanHero from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/SmartCanHero.jpg";
import romanoHero from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/RomanoHero.png";
import leoLogo from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/projectLogos/LEOLogo.svg";
import modifiM1Logo from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/projectLogos/ModifiM1Logo.svg";
import romanoLogo from "/Users/simeonrubin/Desktop/Portfolio/Projects/Portfolio Website/portfolio-website/src/assets/projectLogos/RomanoLogo.svg";

const projects = [
  {
    title: "LEO",
    path: "/leo",
    image: leoHero,
    description: "A smart home system for the elderly",
    logo: leoLogo,
    logoAlt: "LEO Logo",
    color: "#EEE6E0",
  },
  {
    title: "Modifi M1",
    path: "/modifi-m1",
    image: modifiM1Hero,
    description: "High-fidelity, modular headphones",
    logo: modifiM1Logo,
    logoAlt: "Modifi M1 Logo",
    color: "#D3D4DC",
  },
  {
    title: "Smart Canister",
    path: "/smart-canister",
    image: smartCanHero,
    description: "Smart coffee canister with auto vacuum seal",
    color: "#D2D8DC",
  },
  {
    title: "Romano Coffee Grinder",
    path: "/romano-coffee-grinder",
    image: romanoHero,
    description: "Coffee grinder design for a family of premium kitchen products",
    logo: romanoLogo,
    logoAlt: "Romano Logo",
    color: "#EAEBEE",
  },
];

const Carousel = ({ setBackgroundColor }) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const observerRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveIndex(Number(entry.target.dataset.index));
          }
        });
      },
      { 
        threshold: [0.25, 1], // Granular thresholds (0.0, 0.5)
        rootMargin: "50px 0px -50% 0px", // Focus detection exactly around the center
      } 
    );

    const elements = observerRef.current.querySelectorAll(".carousel-item");
    elements.forEach((element) => observer.observe(element));

    return () => {
      elements.forEach((element) => observer.unobserve(element));
    };
  }, []);

  return (
    <div
      ref={observerRef}
      className="md:absolute inset-0 md:overflow-y-scroll scrollbar-hidden flex flex-col items-center space-y-12 md:px-8 md:pt-[30vh] md:pb-[35vh] lg:pt-[30vh] lg:pb-[60vh] snap-y snap-mandatory " // Adds spacing at the top and bottom and enables scroll snapping
    >
      {projects.map((project, index) => (
        <Link to={project.path} key={index}>
          <div
            data-index={index}
            className={`carousel-item w-full max-w-xl flex flex-col items-center group transition-transform duration-500 ease-in-out snap-center ${
              index === activeIndex ? "md:scale-110" : "scale-100"
            }`}
            onMouseEnter={() => setBackgroundColor(project.color)}
            onMouseLeave={() => setBackgroundColor("")}
          >
            {/* Project Image */}
            <div className="relative w-full aspect-[16/9] overflow-hidden rounded-2xl shadow-lg group-hover:scale-110 transition-transform duration-300 ease-in-out z-10">
              {/* Backdrop Blur Effect */}
              <div className="absolute inset-0 bg-black/10 backdrop-blur-sm border border-white/5 opacity-0 group-hover:opacity-100 transition-opacity duration-300 ease-in-out transform scale-105"></div>

              {/* Project Image */}
              <img
                src={project.image}
                alt={project.title}
                className="w-full h-full object-cover"
              />
              {/* Overlay Content */}
              <div className="absolute inset-0 flex flex-row items-center space-x-8 justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300 ease-in-out p-8">
                {project.logo ? (
                  <img
                    src={project.logo}
                    alt={project.logoAlt}
                    style={{ width: "130px", height: "130px" }}
                    className="px-2"
                  />
                ) : (
                  <h2 className="text-[#FFFEF6] text-2xl font-extrabold px-2">
                    {project.title}
                  </h2>
                )}
                <p className="text-[#FFFEF6] text-sm px-2">
                  {project.description}
                </p>
              </div>
            </div>
          </div>
        </Link>
      ))}
    </div>
  );
};

export default Carousel;






